	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Bloglines Down","date":"16 Apr 2005","slug":"/2005-04-16-bloglines-down/","tags":"Y2005"},"html":"<p>I can't log in to my Bloglines accounts this morning... and it seems like <a title=\"Technorati: Search for bloglines\" href=\"http://www.technorati.com/cosmos/search.html?rank=&url=bloglines\">noone else can either</a>. </p>\n<p>Of course, if you depend on Bloglines to manage your reading, you'll never read this.</p>\n<p><strong>Update:</strong> As of 16:15 CET, it seems to be back.</p>"},"next":{"fields":{"title":"Sixty Years After Bergen-Belsen","date":"15 Apr 2005","slug":"/2005-04-15-sixty-years-after-bergenbelsen/"}},"previous":{"fields":{"title":"Double Feature","date":"19 Apr 2005","slug":"/2005-04-19-double-feature/"}}},{"node":{"fields":{"title":"Sixty Years After Bergen-Belsen","date":"15 Apr 2005","slug":"/2005-04-15-sixty-years-after-bergenbelsen/","tags":"Y2005"},"html":"<p><a href=\"http://medienkritik.typepad.com/blog/2005/04/this_day_in_his.html\">David's Medienkritik</a> reminded me that this is the 60th anniversary of the liberation of <a href=\"http://www.bergen-belsen.de/en/\">Bergen-Belsen</a>, located about 50 km from our house. I've <a href=\"http://www.papascott.de/archives/2002/04/11/explaining-the-unexplainable/\" title=\"PapaScott: Explaining the Unexplainable\">mentioned before</a> that Bergen-Belsen is perhaps the eeriest place I've ever been. There are no original buildings from the camp. They were all burned, and there are no replicas. There is a small museum, and various memorials, but most of the grounds consist of heather-covered mounds. Dozens of mounds. Each is a mass grave representing thousands of dead.  The stillness is overwhelming.</p>"},"next":{"fields":{"title":"29560 Unread","date":"14 Apr 2005","slug":"/2005-04-14-29560-unread/"}},"previous":{"fields":{"title":"Bloglines Down","date":"16 Apr 2005","slug":"/2005-04-16-bloglines-down/"}}},{"node":{"fields":{"title":"29560 Unread","date":"14 Apr 2005","slug":"/2005-04-14-29560-unread/","tags":"Y2005"},"html":"<p>I really should check my spam folder more often than every six months...</p>"},"next":{"fields":{"title":"Blue Train","date":"11 Apr 2005","slug":"/2005-04-11-blue-train/"}},"previous":{"fields":{"title":"Sixty Years After Bergen-Belsen","date":"15 Apr 2005","slug":"/2005-04-15-sixty-years-after-bergenbelsen/"}}}],"pathPrefix":"","first":false,"last":false,"index":323,"pageCount":1101}}

